// Background Overlay
// A mixin for getting some background with size cover and optional overlay on top (must be with rgba with opacity)
@mixin background($image, $color1 : null, $color2 : $color1) {
  @if $color1 != null {
    background: linear-gradient($color1, $color2), url('#{$image-path}/#{$image}') no-repeat center / cover; }
  @else {
    background: url('#{$image-path}/#{$image}') no-repeat center / cover; } }
